export const makeRpcMouseEvent = (event) => {
    return {
        pageX: event.pageX,
        pageY: event.pageY,
        button: event.button,
        shiftKey: event.shiftKey,
        ctrlKey: event.ctrlKey,
        metaKey: event.metaKey,
        altKey: event.altKey,
    };
};
export const makeRpcTouchEvent = (event) => {
    return {
        shiftKey: event.shiftKey,
        ctrlKey: event.ctrlKey,
        metaKey: event.metaKey,
        altKey: event.altKey,
        changedTouches: Array.from(event.changedTouches).map((touch) => ({
            pageX: touch.pageX,
            pageY: touch.pageY,
            identifier: touch.identifier,
        })),
    };
};
export const makeRpcWheelEvent = (event) => {
    return {
        pageX: event.pageX,
        pageY: event.pageY,
        button: event.button,
        timeStamp: event.timeStamp,
        deltaMode: event.deltaMode,
        deltaX: event.deltaX,
        deltaY: event.deltaY,
        // @ts-ignore - the wheelDeltaY API is non-standard
        wheelDeltaY: event.wheelDeltaY,
        shiftKey: event.shiftKey,
        ctrlKey: event.ctrlKey,
        metaKey: event.metaKey,
        altKey: event.altKey,
    };
};
export const makeRpcKeyboardEvent = (event) => {
    return {
        keyCode: event.keyCode,
        repeat: event.repeat,
        shiftKey: event.shiftKey,
        ctrlKey: event.ctrlKey,
        metaKey: event.metaKey,
        altKey: event.altKey,
    };
};
